import React from 'react';
import Slider from "react-slick";
import {useDispatch, useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";

import '../prj.css'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {NextArrow, PrevArrow} from '../Arrow'

import {ChangeBackToHome} from "../../../../store/slice";
import zooImage1 from "../../../../styles/prj_image/  (52).png";
import zooImage2 from "../../../../styles/prj_image/  (53).png";
import zooImage3 from "../../../../styles/prj_image/  (54).png";
import zooImage4 from "../../../../styles/prj_image/  (55).png";
import zooImage5 from "../../../../styles/prj_image/  (58).png";


function ZooBot () {

    const dispatch = useDispatch()
    const language = useSelector(state => state.Data.language)
    const theme = useSelector(state => state.Data.theme)
    const navigate = useNavigate();

    const handleClickBackToHome = () => {
        dispatch(ChangeBackToHome())
        navigate('/');
    }

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <NextArrow />,
        prevArrow: <PrevArrow />
    };

    return (
        <>
            <div className="title-container">
                <h3 className={theme ? 'title' : 'title white'}>
                    {language ? 'ZooBot' : ''}
                </h3>
            </div>
            <div className='text-container'>
                <p>  ()</p>
                <p> :</p>
                <p> -       ,     
                     .   ,       .
                </p>
                <ul><p> telegram bot-   :</p>
                    <li>             
                         .</li>
                    <li>          
                          ?.</li>
                    <li>           .</li>
                    <li>         
                            ,     .     
                            .</li>
                    <li>     ,       
                        .           .</li>
                    <li>        ,
                              .</li>
                </ul>
                <a href='https://github.com/anz-mikail/ZooBot_Telegram.git' target='_blank' rel="noreferrer">
                    </a>
            </div>
            <Slider {...settings}>
                <div className="slider-container">
                    <img src={zooImage1} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={zooImage2} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={zooImage3} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={zooImage4} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={zooImage5} className="prj-img" alt='img'/>
                </div>
            </Slider>
            <div className="title-container">
                <p
                    onClick={handleClickBackToHome}
                    className={theme ? 'linkToBack' : 'linkToBack white'}>
                    {!language ? ' ' : 'Back to Home'}
                </p>
            </div>
        </>
    )
}


export default ZooBot;