import React from 'react';
import Slider from "react-slick";
import {useDispatch, useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";

import '../prj.css'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {NextArrow, PrevArrow} from '../Arrow'

import scanImage1 from "../../../../styles/prj_image/  (36).png";
import scanImage2 from "../../../../styles/prj_image/  (37).png";
import scanImage3 from "../../../../styles/prj_image/  (38).png";
import scanImage4 from "../../../../styles/prj_image/  (39).png";
import scanImage5 from "../../../../styles/prj_image/  (40).png";
import scanImage6 from "../../../../styles/prj_image/  (41).png";
import scanImage7 from "../../../../styles/prj_image/  (42).png";
import {ChangeBackToHome} from "../../../../store/slice";


function Scan () {

    const dispatch = useDispatch()
    const language = useSelector(state => state.Data.language)
    const theme = useSelector(state => state.Data.theme)
    const navigate = useNavigate();

    const handleClickBackToHome = () => {
        dispatch(ChangeBackToHome())
        navigate('/');
    }

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <NextArrow />,
        prevArrow: <PrevArrow />
    };

    return (
        <>
            <div className="title-container">
                <h3 className={theme ? 'title' : 'title white'}>
                    {language ? 'Scan project' : ' '}
                </h3>
            </div>
            <div className='text-container'>
                <p> ( )</p>
                <p> :</p>
                <p> -         .
                </p>
                <ul><p> frontend    :</p>
                    <li>      figma.</li>
                    <li>     .</li>
                    <li>    ,    ,  ,
                             .</li>
                    <li> -      .</li>
                    <li>    UX ( ):
                         ,    .</li>
                </ul>
                <a href='https://github.com/anz-mikail/Scan_React.git' target='_blank' rel="noreferrer">
                    </a>
            </div>
            <Slider {...settings}>
                <div className="slider-container">
                    <img src={scanImage1} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={scanImage2} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={scanImage3} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={scanImage4} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={scanImage5} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={scanImage6} className="prj-img" alt='img'/>
                </div>
                <div className="slider-container">
                    <img src={scanImage7} className="prj-img" alt='img'/>
                </div>
            </Slider>
            <div className="title-container">
                <p
                    onClick={handleClickBackToHome}
                    className={theme ? 'linkToBack' : 'linkToBack white'}>
                    {!language ? ' ' : 'Back to Home'}
                </p>
            </div>
        </>
    )
}


export default Scan;